import classes from "./Projects.module.scss";

import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import { scroller } from "react-scroll";

import AOS from "aos";
import "aos/dist/aos.css";

const Projects = () => {
  AOS.init();
  const MainButton = styled(Button)({
    type: "button",
    appearance: "button",
    backfaceVisibility: "hidden",
    backgroundColor: "#EB5E28",
    borderRadius: "0.3rem",
    borderWidth: "0",
    boxShadow:
      "rgba(50, 50, 93, .1) 0 0 0 1px inset,rgba(50, 50, 93, .1) 0 2px 5px 0,rgba(0, 0, 0, .07) 0 1px 1px 0",
    boxSizing: "border-box",
    color: "#fff",
    cursor: "pointer",
    fontSize: "100%",
    height: "3rem",
    lineHeight: 1.15,
    margin: "1rem 0 0",
    outline: "none",
    overflow: "hidden",
    padding: "0 2rem",
    position: "relative",
    textAlign: ["center", "center"],
    textTransform: "none",
    transform: "translateZ(0)",
    transition: "all .2s,box-shadow .08s ease-in",
    userSelect: "none",
    WebkitUserSelect: "none",
    touchAction: "manipulation",
    width: "50%",
    fontFamily: [
      "-apple-system",
      "system-ui",
      "Segoe UI",
      "Roboto",
      "Helvetica Neue",
      "Ubuntu,sans-serif",
    ].join(","),
    "&:disabled": { cursor: "default" },
    "&:hover": { backgroundColor: "#FF672D" },
  });

  const scrollTo = () => {
    scroller.scrollTo("contact", {
      spy: true,
      smooth: true,
      duration: 500,
    });
  };
  return (
    <div data-aos="zoom-in" className={classes.flexBox} name="projects">
      <div className={classes.flexTitle}>
        <div className={classes.square}></div>
        <h1 style={{ color: "var(--white)" }}>Projects</h1>
      </div>
      <div className={classes.flexRow}>
        <h2 style={{ color: "var(--white)" }}>
          As of time, this section is empty
        </h2>
        <MainButton to="contact" onClick={scrollTo}>
          You can help me expand, here
        </MainButton>
      </div>
    </div>
  );
};

export default Projects;
